import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { WSActions, ProviderActions } from '../../actions/state';
import { CertHelper, regExps } from '../../helpers';
import { Button, SelectField, SelectNative, SelectItem, TextField } from '../basic';
import * as BodyStyled from '../create_certificate/styled/body.styled';
import {
  TextareaContainer,
  BtnsContainer,
  TextFieldContainer,
  InputFileContainer,
  LabelStyled,
} from './styled/body.styled';

export default class Body extends Component {

  static propTypes = {
    providers: PropTypes.oneOfType([
      PropTypes.array,
    ]),
  };

  static defaultProps = {
    providers: [],
  };

  static contextTypes = {
    deviceType: PropTypes.string,
    dispatch: PropTypes.func,
    lang: PropTypes.object,
  };

  constructor() {
    super();

    this.state = {
      valid: false,
    };

    this.fieldNodes = {};
  }

  onSelectChange = (data) => {
    const { dispatch } = this.context;
    if (typeof data === 'string') {
      dispatch(ProviderActions.select(data));
    } else {
      dispatch(ProviderActions.select(data.value));
    }
  };

  onClickImportHandler = () => {
    const { dispatch } = this.context;
    const { textarea } = this.fieldNodes;
    const value = textarea.getValue();
    dispatch(WSActions.importItem(value));
  };

  onFileChangeHandler = (e) => {
    const elem = e.target;
    const file = elem.files[0];
    this.fileReaderHandler(file);
    elem.value = '';
  };

  onTextareaChangeHandler = (value, valid) => {
    this.setState({
      valid,
    });
  };

  onDropHandler = (e) => {
    e.stopPropagation();
    e.preventDefault();

    const file = e.dataTransfer.files[0];
    this.fileReaderHandler(file);
  };

  decodeBinaryString(str) {
    const { textarea } = this.fieldNodes;
    try {
      let value = '';
      if (regExps.hex.test(str)) {
        value = str;
      } else if (regExps.base64.test(str)) {
        value = str;
      } else {
        value = CertHelper.formatDer(pvtsutils.Convert.ToHex(pvtsutils.Convert.FromBinary(str)));
      }

      textarea.fieldNode.value = value;
      textarea.validate();
      this.setState({
        valid: textarea.isValid(),
      });
    } catch (e) {
      alert('Cannot decode file.');
    }
  }

  fileReaderHandler(file) {
    const reader = new FileReader();
    const supportedFileExtension = ['csr', 'cer', 'req', 'crt', 'pem'];

    if (!file) {
      return false;
    }

    const fileExtension = file.name.split('.')[file.name.split('.').length - 1];

    if (supportedFileExtension.indexOf(fileExtension) === -1) {
      alert('Not supported file format!');
      return false;
    }

    reader.onerror = () => alert('File cannot be opened');

    if (reader.readAsBinaryString) {
      reader.onload = e => this.decodeBinaryString(e.target.result);
      reader.readAsBinaryString(file);
    } else {
      reader.onload = (e) => {
        const bytes = new Uint8Array(e.target.result);
        const binary = bytes.reduce((acc, byte) => acc + String.fromCharCode(byte), "");

        this.decodeBinaryString(binary);
      };
      reader.readAsArrayBuffer(file);
    }

    return false;
  }

  render() {
    const { providers } = this.props;
    const { valid } = this.state;
    const { deviceType, lang } = this.context;
    const selectedProvider = providers.filter(obj => obj.selected);
    const currentProvider = selectedProvider.length
      ? selectedProvider[0]
      : false;
    const providersFiltered = providers.filter(f => !f.readOnly);

    return (
      <BodyStyled.Body>
        <BodyStyled.Container>
          <TextFieldContainer>
            {
              deviceType === 'phone'
                ? <SelectNative
                  labelText={lang['ImportCertificate.Field.Provider']}
                  placeholder={lang['Select.Label.Provider']}
                  options={providersFiltered.map(item => ({
                    value: item.id,
                    name: item.name,
                  }))}
                  defaultValue={currentProvider ? currentProvider.id : ''}
                  onChange={this.onSelectChange}
                />
                : <SelectField
                  labelText={lang['ImportCertificate.Field.Provider']}
                  placeholder={lang['Select.Label.Provider']}
                  defaultSelected={{
                    name: currentProvider ? currentProvider.name : '',
                    value: currentProvider ? currentProvider.id : '',
                    index: currentProvider ? currentProvider.index : 0,
                  }}
                  onChange={this.onSelectChange}
                >
                  {
                    providersFiltered.map((item, index) => (
                      <SelectItem
                        key={index}
                        value={item.id}
                        primaryText={item.name}
                      />
                    ))
                  }
                </SelectField>
            }
          </TextFieldContainer>
          <TextareaContainer>
            <TextField
              labelText={lang['ImportCertificate.Field.Certificate']}
              multiline
              ref={node => (this.fieldNodes.textarea = node)}
              validation={['hex', 'base64']}
              onChange={this.onTextareaChangeHandler}
              onDrop={this.onDropHandler}
            />
          </TextareaContainer>
          <BtnsContainer>
            <InputFileContainer>
              <LabelStyled htmlFor="file_input">
                {lang['ImportCertificate.Btn.File']}
              </LabelStyled>
              <input
                type="file"
                id="file_input"
                style={{
                  display: 'none',
                }}
                onChange={this.onFileChangeHandler}
              />
            </InputFileContainer>
            <Button
              primary
              onClick={this.onClickImportHandler}
              disabled={!valid}
            >
              {lang['ImportCertificate.Btn.Import']}
            </Button>
          </BtnsContainer>
        </BodyStyled.Container>
      </BodyStyled.Body>
    );
  }
}
