import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { Dialog } from '../basic';

const NumberStyled = styled.div`
  display: inline-block;
  vertical-align: top;
  width: 30px;
  line-height: 40px;
  background: rgba(242, 243, 244, .6);
  border-radius: ${props => props.theme.borderRadius}px;
  font-size: 16px;
  font-weight: 600;
  margin-left: 10px;
  color: ${props => props.theme.dialog.color};
  &:first-child {
    margin-left: 0;
  }
`;

const NumbersContainerStyled = styled.div`
  margin-top: 53px;
  text-align: center;
`;

const DescrStyled = styled.div`
  font-size: 13px;
  line-height: 18px;
  color: ${props => props.theme.dialog.colorDescr};
`;

export default class FortifyAuthorizationDialog extends Component {

  static propTypes = {
    message: PropTypes.string,
  };

  static defaultProps = {
    message: '',
  };

  static contextTypes = {
    lang: PropTypes.object,
  };

  render() {
    const { message } = this.props;
    const { lang } = this.context;

    return (
      <Dialog
        title={''}
        acceptText={''}
        cancelText={''}
      >
        <DescrStyled>
          { lang['Dialog.FortifyAuthorization.Description'] }
        </DescrStyled>
        <NumbersContainerStyled>
          {
            message.split('').map((number, index) => (
              <NumberStyled
                key={index}
              >
                { number }
              </NumberStyled>
            ))
          }
        </NumbersContainerStyled>
      </Dialog>
    );
  }
}
